.leftPanel {
  display: flex;
  height: 100%;
  flex-direction: column;
  overflow: auto;
}

.containerStyle {
  border: 0.1px solid #f19235;
  height: 100%;
  //flex: 2;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.containerStyle:hover {
  .selectedTabContainerStyle {
    background: url('../../../assets/images/HoveredH.png') #f7c05b repeat-x top left;
    width: 100%;
    min-height: 25px;
    border-bottom-width: 0;
  }

  .collapsableContainerStyle {
    border: 3px solid #f7c05b;
    color: #131313;
    border-top-width: 0;
    border-bottom-width: 0;
    flex: 2
  }
}

.selectedTabContainerStyle {
  background: url('../../../assets/images/ActiveH.png') #fce6bf repeat-x top left;
  width: 100%;
  min-height: 25px;
  border-bottom-width: 0;
  cursor: pointer;
}

.collapsableContainerStyle {
  border: 3px solid #fce6bf;
  border-top-width: 0;
  border-bottom-width: 0;
  flex: 2;
}

.tabContainerStyle {
  background: url('../../../assets/images/UnselectedH.png') #7283a6 repeat-x top left;
  border: 0.1px solid;
  border-color: #8d9fc4 #435b8c #435b8c #8d9fc4;
  width: 100%;
  min-height: 25px;
  border-radius: 0;
}

.tabContainerStyle:hover {
  background: url('../../../assets/images/ActiveH.png') #fce6bf repeat-x top left;
  border: 0.1px solid #f19235;
  cursor: pointer;
}

.headerContainerStyle {
  display: flex;
  padding: 3px 5px;
  min-height: 17px;
}

.headerIconTitleContainerStyle {
  width: 90%;
  display: flex
}

.headerTextStyle {
  margin-bottom: 0;
  margin-left: 3px;
  color: #000000 !important;
  font-size: 11.2px !important;
  font-weight: 100;
}

.selectedHeaderTextStyle {
  margin-bottom: 0;
  margin-left: 3px;
  color: #ffffff;
  font-size: 11.2px !important;
  font-weight: 100;
}

.selectedHeaderTextStyle:hover {
  color: #000000;
}

.loadingIconContainerStyle {
  width: 10%;
  justify-content: flex-end;
}

.collapsableDetailsContainerStyle {
  overflow: hidden;
  background: #c6ceda;
  border: 0.1px solid #f19235;
}

.collapsableActivePanel {
  flex: 2;
  height: 100% !important;
  border: 0.1px solid #f19235;
  overflow: hidden;
}

.collapsablePanel {
  flex: 2;
  height: 100% !important;
  overflow: hidden;
}

.loadingGifStyle {
  height: 16px;
  width: 16px;
  background-image: url("../../../assets/images/Loading.gif")
}

.detailsContainerStyle {
  padding: 1px 20px;
  display: flex;
  border: 0.1px solid transparent;
  background: #c6ceda;
  cursor: pointer;
  //min-width: 300px;
  width: 100%;
  white-space: nowrap;
}

.detailsContainerStyle:hover {
  background: url('../../../assets/images/HoveredH.png') #f7c05b repeat-x top left;
  border: 0.1px solid #f19235;
}

.childHeaderTextStyle {
  margin-left: 3px;
  color: #000000 !important;
  font-size: 11.2px !important;
  line-height: 5px !important;
  margin-top: 5px;
  font-weight: 200;
  //padding-right: 10px;
}

.selectedChildHeaderTextStyle {
  color: #000000 !important;
  font-width: 600;
  font-size: 11.2px !important;
  line-height: 5px !important;
  margin-top: 5px;
  margin-left: 3px;
}

.selectedDetailsContainerStyle {
  background: url('../../../assets/images/ActiveH.png') #fce6bf repeat-x top left;
  border: 0.1px solid #f19235;
  padding: 1px 20px;
  display: flex;
  color: #000000 !important;
  cursor: pointer;
  //min-width: 300px;
  width: 100%;
  white-space: nowrap;
}

.collapsableListContainer {
  overflow: auto;
  height: 100% !important;
}
.subDataParentIcon{
  padding-right: 8px;
}