.buttonIcon{
  color: #000000 !important
}

.container{
  height:100%;
  width:100%;
  background-color:#ffffff;
  padding-top: 10px;

  .headerContainer{
    padding:3px 20px;
    position:absolute;
    z-index:0
  }

  .cardContainer{
    padding:10px 10px
  }

  .cardHeader{
    font-weight: bold;
    font-size: 1.2em;
    color: #e08636;
    background-color: #ffffff;
  }

  .cacheConfigSubContainer {
    height:80px;
    width:100%;
    border:1px solid #b5bcc7;
    padding : 15px 10px;
    line-height: 0px;
  }

  .resultSettingsContainer{
    height:150px;
    border:1px solid #b5bcc7;
    padding:20px 10px
  }

  .resultSettingsHeaderContainer{
    display:flex;
    height: 20px;
    width:100%
  }

  .resultSettingsGridContainer{
    flex: 2;
    max-width: 550px;
    height: 17px;
    width: 135px;
    padding: 0px 10px;
  }

  .resultSettingsGrid{
    display:flex;
    margin: 0 ;
    padding: 1px;
    height: 25px;
  }

  .resultSettingsRightSideDetails{
    flex:1;
    justify-content:flex-start;
    padding: 10px 0
  }
  .resultSettingsRightSideText{
    color:#9f0000;
    width: 70%;
    font-weight: 100;
    padding-left: 10px;
  }

  .licenceInfoContainer{
    display:flex;
    height:80px;
    width:100%;
    border:1px solid #b5bcc7;
    padding:30px 10px
  }

  .resultSettingsCol1{
    flex:4;
    display:flex;
    height:25px
  }

  .requiredField{
    color:#9f0000;
    font-weight: bold;
    margin: 0;
  }

  .textLabel{
    font-weight: bold;
    color:#000000;
    margin: 0;
  }

  .inputBox:disabled {
    border: 1px solid #d3d3d3;
    background-color: #efefef;
    background-image: none;
    color: #838281;
    height: 17px;
    width:90%;
    border-radius:0px
  }

  .cacheResName{
    min-width: 160px;
    width: 284px;
  }
  .cacheResValue{
    flex: 4;
  }
}

.loadingGifStyle {
  height: 16px;
  width: 16px;
  background-image: url("../../../assets/images/Loading.gif")
}

.licenceInfoText{
  padding:10px;
  font-weight: 100;
  margin: 0;
}