.parentDiv{
  margin: -10px;
}
.contentDiv{
  padding: 5px;
  border: 1px solid #435b8c;
  background-color: #dfe0e5;
}
.subHeader{
  display: flex;
  background: url("../../../assets/images/Backgrounds.png") #bbc2ce repeat-x 0px -422px;
  border: 1px solid #435b8c;
  margin-bottom: 0;
  padding:5px;
  align-items: center;
  justify-content: space-between;
  cursor: default;
}
.headerLabelBox{
  display: flex;
  align-items: center;
}
.headerText{
  padding-left: 5px;
  font-weight: 600;
}
.buttonStyle{
  color: #000000!important;
  padding: 5px !important;
  background: url("../../../assets/images/Backgrounds.png") #bbc2ce repeat-x 0px -422px !important;
  border: 1px solid transparent !important;
  box-shadow: none !important;
  margin-right: 2px;
}
.errorBox{
  border-color: #d3d3d3;
  background-color: #efefef;
  background-image: none;
  color: #838281;
}
.subContentDiv{
  border: 1px solid #435b8c;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.midContentBox{
  height: 93.5%;
  display: flex;
  flex-grow: 1;
}
.secondReflexDiv{
  display: flex;
  flex-direction: column;
}
.detailsContent{
  background-color: #ffffff;
  border: 1px solid #435b8c;
  border-top: 0px;
  //padding: 5px;
}
.errorDetailsContent{
  border: 1px solid #435b8c;
  border-top-width: 0px;
  background-color: #efefef;
  background-image: none;
  color: #838281;
  padding: 5px;
  &:focus{
    outline:-webkit-focus-ring-color auto 1px !important;
  }
}
.detailsStyle{
  color: #838281;
  font-size: 14px;
}
.test{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 12;
  cursor: default;
}