.companyLogo{
    background: url("../../../assets/images/ExcellerateLogo.png") #ffffff no-repeat 50% 50%;
    min-height: 110px;
    width: 150px;
    background-size: contain;
    margin-top: -10px;
}
.loginBox {
    background-color: #ffffff;
    border: 3px solid #c1c1c1;
    border-radius: 8px 8px;
    width: 600px;
}

.loginHeader{
    display: flex;
    justify-content: space-between;
    padding:10px 0 0 10px !important;
    border-bottom: 2px solid #c1c1c1;
}

.centerBox{
    display: flex;
    flex:1;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.subHeader{
    color: #989898;
    font-family: oxygen;
    font-size: 1.4em;
}
.loginContent{
    background-color: #e6e6e6;
    padding: 8px;
    display: flex;
    flex-direction: column;
    border-bottom: 2px solid #c1c1c1;
}
.textLayout{
    display: flex;
    align-items: center;
    padding: 5px 10px;
}
.textLabel{
    color: #55585a;
    margin-bottom: 0px;
    flex-grow: 2;
    width: 9%;
}
.textField{
    width: 250px;
}
.requiredText{
    display: inline;
    color: #9f0000;
    font-weight: bold;
    margin-right: 5px;
}
.button{
    align-self: center;
    width: 250px;
    margin: 5px 10px;
    font-weight: bold;
    color: #ffffff !important;
    background-position: -100px -100px !important;
    background-color: #0074c3 !important;
    border-color: #0463a3 !important;
    border-radius: 3px !important;
    box-shadow: none !important;
    background: url("../../../assets/images/UnselectedH.png") #7283a6 repeat-x top left;
}
.bottomContent{
    display: flex;
    justify-content: space-between;
}
.bottomText{
    font-size: 12px;
    color:#131313;
    font-weight: 400;
    margin-bottom: 0
}
.footerText{
    font-size: 9px;
    padding: 8px;
    margin:0;
    font-weight: 300;
}
.link{
    color: -webkit-link;
    font-weight: 500;
    &:hover{
        color: -webkit-link;
    }
}
.errorIcon{
    height: 100%;
    display: flex;
    align-items: center;
    margin-right: 5px;
}
.popOver{
    height: 24px;
    display: flex;
    align-items: center;
}
.errorDiv{
    color: #9f0000;
    display: flex;
    justify-content: center;
    height: 20px;
}
