.loginBox {
  background-color: #ffffff;
  border: 3px solid #e6e6e6;
  width: 100%;
}

.inputContainer{
  flex-grow: 3;
  width:250px !important;
  display:flex;
  align-content:flex-start
}

.centerBox{
  display: flex;
  flex:1;
  justify-content: center;
  align-items: center;
  height: 100%;
  border: 1px solid #c1c1c1;
}
.subHeader{
  color: #989898;
  font-family: oxygen;
  font-size: 1.4em;
}
.loginContent{
  background-color: #e6e6e6;
  padding: 8px;
  display: flex;
  flex-direction: column;
}
.textLayout{
  display: flex;
  align-items: center;
  padding: 5px 10px;
}
.textLabel{
  color: #55585a;
  margin-bottom: 0px;
  flex-grow: 2;
  width: 9%;
}
.textField{
  width: 250px;
}
.requiredText{
  display: inline;
  color: #9f0000;
  font-weight: bold;
  margin-right: 5px;
}
.button{
  align-self: center;
  width: 250px;
  margin: 5px 10px;
  font-weight: bold;
  color: #ffffff !important;
  background-position: -100px -100px !important;
  background-color: #0074c3 !important;
  border-color: #0463a3;
  background: url("../../../assets/images/UnselectedH.png") #7283a6 repeat-x top left;
}
.bottomContent{
  display: flex;
  justify-content: space-between;
}
.bottomText{
  font-size: 12px;
  color:#131313;
  font-weight: 400;
  margin-bottom: 0
}
.errorIcon{
  height: 100%;
  display: flex;
  align-items: center;
  margin-right: 5px;
}
.popOver{
  height: 24px;
  display: flex;
  align-items: center;
}
.errorDiv{
  color: #9f0000;
  display: flex;
  justify-content: center;
  height: 20px;
}
